import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			staActice:1,
			form:{
				todayCount:'',
				AllOrderCount:'',
				deliveryCount:''
			}
		}
	},
	onLoad() {
		this.getData(0)
	},
	methods:{
		// 初始化
		getData(n){
			let obj = {
				dateType:n
			}
			axios.post('/sendInformation/querySendInformationTotal',obj).then(res=>{
				this.form = res.data.data;
			})
		},
		preClick(e){
			if(e==1){//下一个日期
				this.getData(2)
			}else{
				this.getData(1)
			}
			this.staActice = e;
		}
	}
}
